.background{
    background: #333 url('../images/backk.jpg') no-repeat center center/cover;
    height: 60vh;
    background-color:rgba(0, 0, 0, 0.1)

}

.header{
    text-align: center;
    font-style: normal;
    font-weight: 900;
    font-size: x-large;
   color: #fff
}

.header .content {
    text-align: center;
    font-family: monospace;
    padding-top: 5px;
    font-weight: 800;
    font-size: large;
    margin-top: 50px;
}
.search h3{
    padding-top: 20px;
}

.header .content p {
    font-weight: 600;
}
.search{
    font-weight: 600;
    font-style: normal;
    font-size: large;
}

.btn{
    padding: 5px 50px;
    margin-top: 15px;
    background: rgb(189, 238, 238);
}

.sheader{
    text-align: center;
}
.sheader h3{
    font-weight: 500;
    padding-bottom: 5px;
}

.sheader h2{
    margin-top: 50px;
    padding-bottom: 1px;
}
.simage{
    text-align: center;
    height: 10rem;
    width: 15rem;

}

.recipe-item{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    padding: 5px;
}

.recipe-text{
    display: inline-block;
    text-align: left;
    padding-right: 10px;
    width: 400px;
}

.recipe-img{
    display: inline-block;
    left: 0;
    width: 100px;
    height: 100px;
    object-fit: cover;
    border: 1px solid black;
    border-radius: 10px;
}

.star {
    cursor: pointer;
    font-size: 20px;
    margin-right: 15px;
}
.login{
    padding-top: 50px;
}
.email{
    padding-top: 10px;
}
.password{
    padding-top: 20px;
}
.raillines
{
    padding-top: 20px 20px;
}
